.product {
  flex: 4;
  padding: 20px;
}

.productTitleContainer {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.productAddButton {
  width: 80px;
  padding: 5px;
  border: none;
  border-radius: 7px;
  font-weight: 600;
  cursor: pointer;
  background-color: teal;
  color: white;
  font-size: 16px;
}
.productTop {
  display: flex;
  align-items:flex-start;
  margin-top: 20px;
}

.productTopLeft,.productTopRight {
  flex: 1;
}
.productTopRight {
  display: flex;
  justify-content: space-between;
  gap: 10px;
  -webkit-box-shadow: 0px 0px 11px -1px #00000059;
    box-shadow: 0px 0px 11px -1px #00000059;
    padding: 20px;
    margin: 20px;
}
.productInfoTop{
    margin-bottom: 10px;    
}
.productInfoImg{
    width: 200px;
    height: 200px;
    border-radius: 15px;
    object-fit: cover;
}
.productInfoBottom{
    display: flex;
    flex-direction: column;
    gap: 10px;
}
.productInfoItem{
    display: flex;
}
.productInfoKey{
    width: 100px;
    font-weight: 600;
}
.productForm{
  display: flex;
  gap: 20px;
  justify-content: space-between;
   -webkit-box-shadow: 0px 0px 11px -1px #00000059;
    box-shadow: 0px 0px 11px -1px #00000059;
    padding: 20px;
    margin: 20px;
}
.ProducFormInput{
  display: flex;
  flex-direction: column;
  gap: 7px;
}
.ProducFormInput>label{
  font-weight: 600;

}
.ProducFormInput>input{
  border: none;
  padding: 10px 15px;
  border-bottom: 1px solid #555;
  outline: 0;
  margin-bottom: 20px;
}


.productUpdateImg{
  width: 150px;
  height: 150px;
  border-radius: 10px;
  object-fit: cover;
}
.productFromRight{
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.productUpdateUpload{
  display: flex;
  align-items: center;
}
.productUpdateIcon{
  cursor: pointer;
  margin-left: 20px;
}
.productUpdateIcon{
  border: none;
  background-color: darkblue;
  color: white;
  font-weight: 600;
  cursor: pointer;
}