.widgetSm{
    flex: 1;
    -webkit-box-shadow: 0px 0px 11px -1px #00000059;
    box-shadow: 0px 0px 11px -1px #00000059;
    padding: 20px;
    margin-right: 20px;
}

.widgetSmTitle{
    font-style: 32px;
    font-weight: 600;
}
.widgetSmImg{
    font: 40px;
    height: 40px;
    width: 40px;
    border-radius: 50%;
    object-fit: cover;
}
.widgetSmList{
    margin: 0;
    padding: 0;
}
.widgetSmListItem{
    display: flex;
    justify-content:space-between ;
    align-items: center;
    margin: 20px 0;
}
.widgetSmUser{
    display: flex;
    flex-direction: column;
}
.widgetSmUserName{
    font-weight: 600;
}
.widgetSmUserTitle{
    font-weight: 300;
}
.widgetSmButton{
    display: flex;
    align-items: center;
    border: none;
    border-radius: 10px;
    padding: 7px 10px;
    cursor: pointer;
    background-color: #eeeef7;
    color: #555;
}
.widgetSmIcon{
    font-style: 16px;
    margin-right: 5px;
}