.widgetLg{
    flex: 2;
    -webkit-box-shadow: 0px 0px 11px -1px #00000059;
    box-shadow: 0px 0px 11px -1px #00000059;
    padding: 20px;
    
}
.widgetLgTable{
    width: 100%;
    border-spacing: 20px;
}
.widgetLgTitle{
    font-style: 22px;
    font-weight: 600;
}
.widgetLgImg{
    width: 40px;
    height: 40px;
    border-radius: 50%;
    object-fit: cover;
}
.widgetLgbutton{
    padding: 5px 7px;
    border: none;
    border-radius: 10px;
}
.widgetLgbutton.Approved{
    background-color: #e5faf2;
    color: #3bb077;
}
.widgetLgbutton.Declined{
    background-color: #fff0f1;
    color: #d95087;
}
.widgetLgbutton.Pending{
    background-color: #ebf1fe;
    color: #2a7ade;
}
.widgetLgTh{
    text-align: left;
}
.widgetLgUser{
    display: flex;
    align-items: center;
    gap: 10px;
    font-weight: 600;
}
.widgetLgDate, .widgetLgAmount{
    font-weight: 300;
}