.user{
    flex: 4;
    padding: 20px;
}

.userTitleContainer{
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.userAddButton{
    width: 80px;
    padding: 5px;
    border: none;
    border-radius: 7px;
    font-weight: 600;
    cursor: pointer;
    background-color: teal;
    color: white;
    font-size: 16px;
}

.userContainer{
    display: flex;
    gap: 10px;
}
.userShow{
    flex: 1;
    padding: 20px;
    -webkit-box-shadow: 0px 0px 11px -1px #00000059;
    box-shadow: 0px 0px 11px -1px #00000059;
}
.userUpdate{
    flex: 2;
    padding: 20px;
    -webkit-box-shadow: 0px 0px 11px -1px #00000059;
    box-shadow: 0px 0px 11px -1px #00000059;
}

.userShowTop{
    display: flex;
    align-items: center;
    gap: 30px;
    margin-bottom: 20px;
}
.userShowImg{
    width: 40px;
    height: 40px;
    border-radius: 50%;
    object-fit: cover;
}
.userShowTopTitle{
    display: flex;
    flex-direction: column;
}
.userShowUsername{
    font-weight: 600;
}
.userShowUserTitle{
    font-weight: 300;
}

.userShowInfo{
    display: flex;
    align-items: center;
    gap: 20px;
    color: #444;
    padding: 15px;
}
.userShowIcon{
    font-size: 16px;
}
.userShowTitle{
    font-weight: 600;
    color: #555;
}


.userUpdateImg{
    width: 100px;
    height: 100px;
    border-radius: 10px;
    object-fit: cover;
    margin-right: 20px;
}
.userUpdateTitle{
    font-weight: 600;
    font-size: 24px;
}
.userUpdateFrom{
    display: flex;
    justify-content: space-between;
    margin-top: 20px;
}

.userUpdateItem{
    display: flex;  flex-direction: column;
    gap: 5px;
    margin-top: 10px;
}
.userUpdateItem>label{
    margin-bottom: 5px;
    font-size: 14px;
}
.userUpdateInput{
    border: none;
    width: 250px;
    border-bottom: 1px solid gray;
    padding: 10px 0;
    outline: 0;
}
.userUpdateRight{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}
.userUpdateUpload{
    display: flex;
    align-items: center;
}
.userUpdateIcon{
    cursor: pointer;
}
.userUpdateButton{
    border-radius: 5px;
    border: none;
    cursor: pointer;
    padding: 5px;
    background-color: darkblue;
    color: white;
    font-weight: 600;
}