.userList{
    flex:4;
}
.userListUser{
    display: flex;
    align-items: center;
    gap: 10px;
}
.userListImg{
    width: 40px;
    height: 40px;
    border-radius: 50%;
    object-fit: cover;
}
.userEditor{
    display: flex;
    align-items: center;
    gap: 15px;
}
.userListEdit{
    padding: 7px 10px ;
    border: none;
    font-weight: 600;
    font-size: 14px;
    border-radius: 10px;
    background-color: #3bb077;
    color: white;
    cursor: pointer;
}
.userListIcon{
    color: red;
    cursor: pointer;
}