.featured {
  width: 100%;
  display: flex;
  justify-content: space-between;
}
.featuredItem {
  flex: 1;
  margin: 0 20px;
  padding: 30px;
  border-radius: 10px;
  cursor: pointer;
  -webkit-box-shadow: 0px 0px 11px -1px #00000059;
  box-shadow: 0px 0px 11px -1px #00000059;
}
.featuredTitle {
  font-size: 20px;
}
.featuredMonyContainer {
  margin: 10px 0;
  display: flex;
  align-items: center;
}
.featuredMoney {
  font-size: 30px;
  font-weight: bold;
}
.featuredMoneyRate {
  display: flex;
  align-items: center;
  margin-left: 20px;
}
.featuredIcon {
  font-style: 14px;
  margin-left: 5px;
  color: green;
}
.featuredIcon.negative {
  color: red;
}
.featuredSub {
  font-style: 15px;
  color: gray;
}
