.newUser{
    flex: 4;
}
.newUserForm{
    display: flex;
    flex-wrap: wrap;
}
.newUserItem{
    width: 500px;
    display: flex;
    flex-direction: column;
    margin-top: 20px;
    gap: 5px;
    margin-right: 20px;
}

.newUserItem>Input{
    border: none;
    padding: 10px;
    border:1px solid gray ;
    border-radius: 5px;
    outline: 0;
    color: rgb(151,150,150);
}
.newUserItem>label{
    color: #555;
    font-weight: 600;
}
.newUserGander>label{
    margin-top: 50px;
    font-size: 18px;
    margin: 0 20px 0 10px;
    color: #555;
}
.newUserSelect{
    padding: 10px;
    border-radius: 5px;
}
.newUserButton{
    width: 200px;
    border: none;
    background-color: darkblue;
    color: white;
    padding: 7px 10px;
    font-weight: 600;
    margin-top: 30px;
    border-radius: 10px;
    cursor: pointer;
}