.sidebar {
  flex: 1;
  height: calc(100vh - 50px);
  background-color: rgb(251, 251, 255);
  position: sticky;
  top: 50px;
}
.sidebarWrapper {
  padding: 20px;
  color: #555;
}
.sidebarMenu {
  margin-bottom: 20px;
}
.sibarTitle {
  font-size: 13px;
  color: rgb(187, 186, 186);
  padding: 10px 0;
}
.sidebarList {
  padding: 5px;
}
.sidebarList a{
  color: inherit;
}
.sidebarListItem {
  padding: 5px;
  cursor: pointer;
  display: flex;
  align-items: center;
  border-radius: 10px;
}
.sidebarListItem:hover {
  background-color: rgb(228, 228, 250);
}
.sidebarIcon {
  margin-right: 5px;
  font-size: 20px !important;
}
a.active > li{
  background-color: rgb(228, 228, 250);
}